.datetimepicker {
	padding: 16px 18px 10px;
	direction: ltr;
}

.datetimepicker-inline {
	/*width: 300px;*/
}

.datetimepicker.datetimepicker-rtl {
	direction: rtl;
}

.datetimepicker.datetimepicker-rtl table tr td span {
	float: right;
}

.datetimepicker-dropdown, .datetimepicker-dropdown-left {
	top: 0;
	left: 0;
}

[class*=" datetimepicker-dropdown"]:before {
	content: '';
	display: inline-block;
	border-left: 7px solid transparent;
	border-right: 7px solid transparent;
	border-bottom: 7px solid #cccccc;
	border-bottom-color: rgba(0, 0, 0, 0.2);
	position: absolute;
}

[class*=" datetimepicker-dropdown"]:after {
	content: '';
	display: inline-block;
	border-left: 6px solid transparent;
	border-right: 6px solid transparent;
	border-bottom: 6px solid #ffffff;
	position: absolute;
}

[class*=" datetimepicker-dropdown-top"]:before {
	content: '';
	display: inline-block;
	border-left: 7px solid transparent;
	border-right: 7px solid transparent;
	border-top: 7px solid #cccccc;
	border-top-color: rgba(0, 0, 0, 0.2);
	border-bottom: 0;
}

[class*=" datetimepicker-dropdown-top"]:after {
	content: '';
	display: inline-block;
	border-left: 6px solid transparent;
	border-right: 6px solid transparent;
	border-top: 6px solid #ffffff;
	border-bottom: 0;
}

.datetimepicker-dropdown-bottom-left:before {
	top: -7px;
	right: 6px;
}

.datetimepicker-dropdown-bottom-left:after {
	top: -6px;
	right: 7px;
}

.datetimepicker-dropdown-bottom-right:before {
	top: -7px;
	left: 6px;
}

.datetimepicker-dropdown-bottom-right:after {
	top: -6px;
	left: 7px;
}

.datetimepicker-dropdown-top-left:before {
	bottom: -7px;
	right: 6px;
}

.datetimepicker-dropdown-top-left:after {
	bottom: -6px;
	right: 7px;
}

.datetimepicker-dropdown-top-right:before {
	bottom: -7px;
	left: 6px;
}

.datetimepicker-dropdown-top-right:after {
	bottom: -6px;
	left: 7px;
}

.datetimepicker > div {
	display: none;
}

.datetimepicker.minutes div.datetimepicker-minutes {
	display: block;
}

.datetimepicker.hours div.datetimepicker-hours {
	display: block;
}

.datetimepicker.days div.datetimepicker-days {
	display: block;
}

.datetimepicker.months div.datetimepicker-months {
	display: block;
}

.datetimepicker.years div.datetimepicker-years {
	display: block;
}




.datetimepicker table{
	border-collapse: separate;
	border-collapse: initial;
	border-spacing: 0;
	margin: 0;
    width: 252px;
}

.datetimepicker tbody tr,
.datetimepicker tbody tr:hover{
	text-align: center;
	height: 36px;
	background: transparent;
}

.datetimepicker tbody td{
	width: 36px;
	height: 36px;
	padding: 0;
	text-align: center;
	border: 1px solid transparent;
	border-radius: 4px;
	cursor: pointer;
}
.datetimepicker tbody td:hover{
	border-color: #0375BD;
	background: transparent;
}



.datetimepicker tbody td.old,
.datetimepicker tbody td.new {
	color: #999;
}

.datetimepicker table tr td.disabled,
.datetimepicker table tr td.disabled:hover {
	background: #efefef;
	color: #ccc;
	cursor: default;
}

.datetimepicker tbody td.today,
.datetimepicker tbody td.today:hover,
.datetimepicker tbody td.today.disabled,
.datetimepicker tbody td.today.disabled:hover {
	border-color: #0375BD;
	background: #fff;
}

.datetimepicker tbody td.active,
.datetimepicker tbody td.active:hover,
.datetimepicker tbody td.active.disabled,
.datetimepicker tbody td.active.disabled:hover {
	background-color: #0375BD;
	border-color: #0375BD;
	color: #ffffff;
}

