.datetimepicker thead tr,
.datetimepicker thead tr:hover{
	text-align: center;
	height: 26px;
	background: transparent;
	color: #404040;
}

.datetimepicker thead th,
.datetimepicker thead th:hover{
	text-align: center;
	height: 26px;
	vertical-align: middle;
	font-size: 13px;
	border-radius: 4px;
	cursor: default;
}


.datetimepicker thead th.switch,
.datetimepicker thead th.switch:hover {
	width: inherit;
	font-size: 17px;
	cursor: pointer;
}
.datetimepicker thead th.switch:hover {
	background-color: #F2F2F2;
}

.datetimepicker th.prev,
.datetimepicker th.next{
	line-height: 28px;
	cursor: pointer;
}

.datetimepicker th.prev:hover,
.datetimepicker th.next:hover{
	background-color: #F2F2F2;
}

.datetimepicker .icon-arrow-left,
.datetimepicker .icon-arrow-right{
	display: inline-block;
	width: 100%;
	text-align: center;
	line-height: 28px;
	cursor: pointer;
}

.datetimepicker .icon-arrow-left:after,
.datetimepicker .icon-arrow-right:after{
	 content: '\003C';
	 display: inline-block;
	 color:  #0375BD;
	 font-size: 18px;
	 transform: scaleY(1.5);
}

.datetimepicker .icon-arrow-right:after{
	 content: '\003E';
}

.datetimepicker tfoot tr{
    padding-top: 5px;
}
.datetimepicker tfoot tr:hover{
    background-color: #F2F2F2;
}

.datetimepicker tfoot th{
    height: 25px;
    vertical-align: middle;
    text-align: center;
    color: #0175BD;
    font-size: 13px;
    font-weight: 400;
    cursor: pointer;
    border-radius: 4px;
}


.datetimepicker-months tbody td:hover,
.datetimepicker-years tbody td:hover{
    border-color: transparent;
}
.datetimepicker-months tbody td span,
.datetimepicker-years tbody td span{
    width: 83px;
    height: 36px;
    border: 1px solid transparent;
    line-height: 36px;
    float: left;
    border-radius: 4px;
}
.datetimepicker-months tbody td span.active,
.datetimepicker-months tbody td span:hover,
.datetimepicker-years tbody td span.active,
.datetimepicker-years tbody td span:hover{
    border-color: #0375BD;
}


.datetimepicker th.clear,
.datetimepicker td.clear{
	display: none;
}
