}
::-webkit-scrollbar-corner {
    background: rgba(0,0,0,0);
}



/* loader */
.l-loader {
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.1s, opacity 0.1s linear;
}
.l-loader.visible-loader {
    visibility: visible;
    opacity: 1;
    transition: visibility 0.1s, opacity 0.1s linear;
}
.l-loader:not(:empty) {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: rgba(255, 255, 255, .65);
    z-index: 100;
}


.datalist-panel .l-loader:not(:empty)  {
    z-index: 601;
}

.loader {
    width: 100%;
    height: 28px;
    margin: -12px 0 0;
    line-height: 24px;
    font-size: 12px;
    color: #0575bd;
    font-weight: 600;
    display: block;
    text-align: center;
    position: absolute;
    top: 50%;
    background: 50% 100% no-repeat url('data:image/gif;base64,');
}
