/* --------------- Search ----------------*/
.tr-search {
    margin: 0;
    display: none;
    position: relative;
    box-sizing: content-box;
    z-index: 44;
}

.tr-search_report {
    width: 50%;
    margin: 0;
    float: right;
    display: block;
    border: 0;
}

.tr-search_mselect {
    margin: 0;
    display: block;
}

.tr-search__input {
    width: 100%;
    margin: 0;
    border: 0;
    outline: 0;
    display: block;
    transition: background, border-color .2s ease-in-out;
}

.tr-search__placeholder {
    width: 100%;
    padding-left: 0;
    padding-right: inherit;
    font-size: 14px;
    position: absolute;
    top: 13px !important;
    left: 30px !important;
    color: #bcc6cf;
    cursor: text;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    display: block;
}

.tr-search__input:focus + .tr-search__placeholder,
.tr-search__input:valid + .tr-search__placeholder {
    display: none;
}

.tr-search__placeholder.focused {
    display: none;
}

.tr-search__clear {
    position: absolute;
    cursor: pointer;
    transform: translateY(-50%);
}


.tr-search__clear:hover {
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
    filter: alpha(opacity=100);
    opacity: 1;
}
