/* List/Grid CSS */

.list-item {
    height: 34px;
    padding: 0 5px;
    line-height: 34px;
    font-size: 13px;
    font-weight: normal;
    color: #43525d;
    position: relative;
    overflow: hidden;
    white-space: nowrap;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    cursor: pointer;
}
.list-item:hover {
    background: #f5f5f5;
}

.list {
    height: 100%;
    overflow: auto;
    display: flex;
    flex: 1;
}

.visible-collection {
    height: 100%;
    width: auto;
}

.visible-collection-wrp, .selection-panel-wrp {
    position: relative;
    will-change: top;
}

.visible-collection-wrp.empty{
    height: 100%;
}

.visible-collection-view {
    height: inherit;
}

.visible-collection-view:focus {
    outline: none;
}


.grid-content-wrp {
    display: table;
    min-width: 100%;
    border-collapse: separate;
    table-layout: fixed;
    position: relative;
    will-change: top;
    contain: content;
}

.grid-content-wrp.grid-content-wrp_width-auto {
    min-width: auto;
}

.hidden-columns .grid-content-wrp {
    min-width: 100% !important;
}

.grid-header-wrp {
    display: flex;
    flex: 0 0 auto;
    overflow-y: scroll;
    -ms-scrollbar-arrow-color: white;
    -ms-scrollbar-track-color: white;
    z-index: 1;
}

.grid-header > .cell_selection {
    box-shadow: inset -1px 0 0 0 #90c3eb, inset 1px 1px 0 0 #0575bd;
    background-color: #0575bd;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 2;
    padding: 0;
}

.grid-header > .cell_selection > .checkbox,
.grid-header > .cell_selection-index > .checkbox {
    z-index: 1
}

.grid-header:hover > .cell_selection {
    background-color: #0575bd;
}

.grid-content-view {
    flex: 1 1 auto;
    overflow: scroll;
}

.grid-selection-panel-region {
    overflow: hidden;
    flex-shrink: 0;
    overflow-x: scroll;
    -ms-scrollbar-arrow-color: white;
    -ms-scrollbar-track-color: white;
}

.grid-selection-panel {
    position: relative;
    display: flex;
    flex-direction: column;
}

.grid-content-view > .list-view {
    height: inherit;
}

.grid .visible-collection {
    table-layout: fixed;
    width: 100%;
    border-collapse: collapse;
}


.grid-cell {
    display: inline-block;
    border-bottom: 1px solid #dfe4e8;
    padding-right: 15px;
    height: 32px;
    line-height: 32px;
    vertical-align: middle;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 12px;
    color: #000;
    cursor: default;
    min-width: 50px;
}

.grid-selection-cell-all {
    flex: none;
}

.grid-selection-cell-all:empty {
    display: none;
}

.grid-selection-cell-all .cell_selection{
    position: relative;
}

.selection-cell-wrp {
    display: flex;
    position: absolute;
    height: 25px;
    line-height: 25px;
    width: 30px;
}

.selection-cell-dots {
    width: 25px;
    height: 20px;
    cursor: move;
    position: absolute;
    left: 24px;
}

.selection-cell-index {
    width: 30px;
    overflow: hidden;
    text-overflow: ellipsis;
    left: 33px;
    position: absolute;
}

.selection-cell-dots circle{
    fill: #888E94
}


.grid-selection-index {
    width: 55px;
}

.grid-header-view {
    position: relative;
    border-collapse: separate;
    display: table-header-group;
}

.grid-header-column-title {
    flex: 1;
    overflow: hidden;
    text-overflow: ellipsis;
}

.grid-header-view:empty {
    display: none;
}

.grid-header-column {
    min-width: 50px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    text-align: left;
    cursor: default;
    height: 35px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 2;
    background-clip: padding-box;
    max-width: 0px; /*hack to set width less than cells content */
}

.header-column-wrp{
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
}

.grid-header-dragger {
    position: absolute;
    touch-action: none;
    right: 0;
    top: 0;
    bottom: 0;
    width: 8px;
    cursor: e-resize;
    z-index: 3;
    opacity: .8;
}

.grid-header-dragger:before{
    content: '';
    width: 1px;
    height: 15px;
    position: absolute;
    left: 1px;
    top:50%;
    margin-top: -7px;
    visibility: hidden;
}

.grid-header-dragger:before{
    background-color: #fff;
}

.grid-header:hover .grid-header-dragger:before{
    visibility: visible;
}

.sort_down, .sort_up {
    display: inline-block;
    position: relative;
    background-repeat: no-repeat;
    flex-shrink: 0;
}

.filter-sort-btn {
    display: inline-block;
    width: 14px;
    height: 14px;
    position: relative;
    top: 2px;
    background-image: url('data:image/png;base64,');
}

.has-filter .filter-sort-btn{
    background-image: url('data:image/png;base64,');
}


/* Scrollbar */

.scrollbar-region{
    position: absolute;
    width: 10px;
    right: 0;
    bottom: 0;
    top: 46px;
}
.scrollbar {
    height: 100%;
    background-color: #fcfcfc;
    z-index: 1;
    position: absolute;
    width: 12px;
}
.dragger {
    position: relative;
    top: 0;
    height: 10%;
    background-color: #d9d7d7;
    opacity: 0.3;
}

.scrollbar.hover {
    background-color: #F0F0F0;
}

.scrollbar.hover .dragger {
    background-color: #B9B9B9;
    opacity: 1;
}

.scrollbar .dragger.active {
    background-color: #A0A0A0;
}

/* Empty View */
.empty-view {
    width: 100%;
    height: 100%;
    color: #70787F;
    text-align: center;
    min-height: 25px;
}
tr.empty-view {
    display: table-row;
}
.empty-view_text {
    text-align: center;
    white-space: nowrap;
    line-height: 25px;
}
/* /Empty View */

/* Editable Grid */
.grid-container{
    position: relative;
    display: flex;
    flex-direction: column;
    flex: 1;
    max-width: 100%;
    max-height: 100%;
}

.modal-window-wrapper .grid-container{
    max-height: 100%;
}

.layout__vertical-layout > .grid-container{
   flex: 1 1 auto;
}

.grid-container > .form-label {
    flex: none;
}

.grid-toolbar-wrp{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex: none;
}

.grid-toolbar-region {
    flex: 1 1 auto;
    overflow: hidden;
}

.columns-select-list .grid-toolbar__search{
    flex: 0 0 auto;
    width: 100%;
}

.columns-select-list .grid-toolbar-wrp >.grid-toolbar__search{
    order: initial;
}

.columns-select-list .grid-toolbar__search .tr-search{
    border: none;
    width: 100%;
}

.editable-grid-field {
    width: 100%;
    height: 25px;
}

.editable-grid-field .form-editor{
    width: 100%;
    height: 100%;
}

.editable-grid-field .editor {
    height: 24px;
    line-height: 24px;
    color: #000;
    padding: 0 5px;
    border-bottom: none;
}

.editable-grid-field .editor:after {
    display: none;
    background: transparent;
}

.editable-grid-field.readonly .editor,
.editable-grid-field.disabled .editor,
.editable-grid-field .editor_checkbox{
    outline: none;
}

.editable-cell input {
    background-color: transparent;
    border-bottom: none;
}

.editable-cell .input_dropdown {
    border-bottom: none;
}

.grid-cell_last .editable-cell{
    margin-right: 45px;
}

.cke_button_label {
    display: none !important;
}

.editable-grid-field.error .form-label__error,
.editable-grid-field.warning .form-label__error{
    position: absolute;
    left: 0;
    top: 0;
    width: 8px;
    height: 8px;
    margin:0;
}
.editable-grid-field .form-label__error:before {
    content: '';
    border: 4px solid transparent;
    border-top: 4px solid #e61212;
    border-left: 4px solid #e61212;
    background: transparent;
    border-radius: 0;
    top: 1px;
    left: 1px;
    width: auto;
    height: auto;
}

.editable-grid-field .form-label__error-button {
    height: 8px;
    width: 8px;
    vertical-align: top;
}

.editable-grid-field .form-label__error-button-anchor {
    width: 8px;
    height: 8px;
    top: 0;
    left: 0;
    bottom: auto;
}

/* /Editable Grid */

/* New grid */

.cell_editable {
    display: flex;
    align-items: center;
}

.cell.readonly {
    background-color: #f9f9f9;
}

.cell_selection, .cell_selection-index {
    align-items: center;
    /*height: 35px;
    line-height: 35px;*/
}

.cell_selection {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    left: 0;
    z-index: 1;
    background-color: #fff;
    background-clip: padding-box;
}
tr.row-checked .cell_selection{
    background-color: #c2d7e5;
}
tr.selected .cell_selection{
    background-color: #eaf3f8;
}
tr:hover .cell_selection {
    background-color: #efefef
}

.disabled.cell-focused {
    outline: none;
}

.cell.selected{
    outline-offset: -1px;
}

.cell .editor{
    height: 100%;
    line-height: normal;
    line-height: initial;
    border: none;
    background-color: transparent;
    flex: auto;
}
tr.dragover:not(.grid-header) td {
    border-bottom: 1px solid #ff0000;
}
tr.grid-header.dragover td {
    box-shadow: inset 0px -1px #ff0000;
}

.dragover .cell *,
.dragover.cell_draggable *,
.dragover .grid-header-column *,
.dragover .cell_selection-index *,
.dragover .cell_selection * {
    pointer-events: none;
}

.cell > .editor:after{
    display: none;
}

.cell .editor_checkbox.editor_focused{
    outline: none;
}

.cell .editor_checkbox {
    display: flex;
    align-items: center;
}

.cell .input{
    border: none;
    height: 100%;
    line-height: inherit;
    background-color: transparent;
    font-size: 13px;
}
.cell .textarea{
    border: none;
    height: 100%;
    line-height: 35px;
    background-color: transparent;
    font-size: 13px;
    white-space: pre;
    padding: 0;
}

.cell a{
    font-size: 13px;
}

.cell p{
    margin-bottom: 0;
}

.cell__dots{
    visibility: hidden;
    z-index: 1;
    cursor: move;
    height: 35px;
}

.cell_selection:hover .cell__dots,
.cell_selection-index:hover .cell__dots{
    visibility: visible;
}

.cell__dots svg{
    fill: #c9caca;
}

.cell__index {
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 13px;
    background-clip: padding-box;
    margin-right: 25px;
    margin-left: 3px;
}

.cell-right, .grid-header-right {
    text-align: right;
}

.cell.editor_bubble .bubbles__i{
    height: auto;
}

.cell > .task-links__link  {
    display: inline;
    display: initial;
}

/*-- Compact list --*/
.compact.grid-container .cell .input,
.compact.grid-container .cell .bubbles__input,
.compact.grid-container .cell a{
    font-size: 12px;
}

.compact.grid-container .grid-header,
.compact.grid-container .cell_selection-index,
.compact.grid-container .cell_selection,
.compact.grid-container .cell,
.compact.grid-container .grid-header-column{
    height: 26px;
